<script>
	import Scroller from '@sveltejs/svelte-scroller';
	import { Email, Reddit, LinkedIn, WhatsApp, Twitter } from 'svelte-share-buttons-component';
	import { csv } from 'd3-fetch';
	import Bubbles from "./Bubbles.svelte";
	import DetailCard from "./DetailCard.svelte";

	const url = 'https://pchynoweth.github.io/svelte-share-buttons-component/';
	const title = 'Svelte Share Buttons Component';
	const desc = 'Svelte based social media share buttons component with no tracking.';

	// all these are for the scroller
	let index, offset, progress;

	// this controls the grouping for bubbles
	let view;

	// responsive width for bubble chart
	let width;
	let mobile;

	// store state of toggle btn
	let active = false;

	const scrollerVizId = "scroller-bubbles"
	const filterVizId = "filter-bubbles"
	const headerVizId = "header-bubbles"

	// this is reactive, so it runs whenever index changes
	$: if (index == 0) {
		view = "all"
	} else if (index == 1) {
		view="rating"
	} else if (index == 2) {
		view="genre"
	} else if (index == 3) {
		view="month"
	}

	$: if (width < 600) {
		mobile = true
	} else {
		mobile = false
	}

	// load the data
	let data;
	async function load() {
		const dataArr = [];
		data = await csv('data/goodreads.csv', d => { 
		  const returnObj = {
			title: d.title,
			author: d.fullname,
			pages: +d.pages,
			rating: +d.rating,
			month: d.month,
			review: d.review,
			genre: d.genre,
			topic: d.topics,
			cover: d.cover
		  };
		returnObj['dataArr'] = dataArr;
		return returnObj;
	});
  }
  load();
  let bookData;
  
  function handleClick(change) {
	view = change
  }

  // assign data from a bubble to bookData
  // to be passed down into DetailCard
  const showDetail = (e) => {
		bookData = e.detail
	}

</script>

<main>
	<div id={headerVizId} class="header-graphic" bind:clientWidth={width}></div>
		<div>
		{#if width}
			{#if data}
				<Bubbles current="intro" dataset={data} w={width} rootid={headerVizId}></Bubbles>
			{/if}
		{/if}
	</div>
	<div class='{mobile ? "mobile-top hero-title": "hero-title"}'>
		<h1>To all the books I read before (in 2020)</h1>
	</div>
	<article class="intro-text">
		<p>
			How to survive a year of isolation, social distancing, Zoom-fatigue, and doom scrolling?
		</p>
		<p>
			Books.
		</p>
		<p>At least that’s what I tried. In the miserable year of 2020 (may it rest fitfully), I turned to books as a quiet solace. I did not have the words to express what I wanted, needed, felt. So I turned to the words of others.</p>
		<p>In books we find a portal to new worlds. Some books help us to escape, while others hold up a mirror to our own world.</p>
		<aside>
			psst...anytime you see the <span class="glasses-icon">&#10175</span>, you can click to find the book on Goodreads!
		</aside>
		<p>I read <span class="pill" style="background-color: rgba(255, 145, 75,0.5)">memoirs</span> and <span class="pill" style="background-color: rgba(4, 70, 74, 0.3)">science fiction</span> and <span class="pill" style="background-color: rgba(154, 210, 218, 0.5)">graphic novels</span>. I learned about the <a target="_blank" href="https://www.goodreads.com/search?q=The%20Tea%20Girl%20of%20Hummingbird%20Lane"><span class="glasses-icon">&#10175</span>ancient tradition of tea brewing</a> in the mountain villages of China. I reveled in the <a arget="_blank" href="https://www.goodreads.com/book/show/40180098-the-overstory?from_search=true&from_srp=true&qid=BNCxu3Q35z&rank=1"><span class="glasses-icon">&#10175</span>magic of redwood forests</a>. I let my soul be swallowed by poems of grief, loss, and hope.</p>
		<p>If reading is an act of learning, then writing must be an act of remembering. I read some incredible books this year, ones that I hope to never forget. So this interactive essay will act as a commemoration, a bookend of sorts, but also a practice in remembering.</p>
		<p>To all the books I read last year: thank you. Your pages pulled me through.</p>
	</article>
	<Scroller top={0.2} bottom={0.8} bind:index bind:offset bind:progress>
		<div slot="background">
		  <!-- <p>
			This is the background content. It will stay fixed
			in place while the foreground scrolls over the top.
		  </p> -->
		  <div id={scrollerVizId} bind:clientWidth={width}>
			{#if width}
				{#if data}
					<Bubbles current={view} dataset={data} w={width} rootid={scrollerVizId}></Bubbles>
				{/if}
			{/if}
			</div>
		</div>
	  
		<div slot="foreground">
			<section>
			<div class="scrolly-box">
				<div class="scrolly-text">
					<p>In 2020, I read a total of <span class="bold-text">40 books</span>.</p>
				</div>
			</div>	  
			</section>
		  <section>
			<div class="scrolly-box">
				<div class="scrolly-text">
					<p>For the most part, I read some very good books in 2020. This is what they look like sorted by my Goodreads rating. Over half received a 4&#10029 or 5&#10029.</p>
				</div>
			</div>
			</section>
		  <section>
			<div class="scrolly-box">
				<div class="scrolly-text">
					<p>I was not picky with genres. I wanted to read widely and find new ideas in undiscovered realms. Last year I read my first <span class="pill" style="background-color: rgba(154, 210, 218, 0.5)">graphic novel</span> and absolutely loved it (<a arget="_blank" href="https://www.goodreads.com/search?q=Blankets"><span class="glasses-icon">&#10175</span>Blankets</a>).</p>
				</div>
			</div>
		  </section>
		  <section>
			<div class="scrolly-box">
				<div class="scrolly-text">
					<p>Throughout the year, I read more or less consistently. Summer months were the slowest. I read the most books in the fall months of September and October.</p>
				</div>
			</div>
		  </section>
		</div>
	  </Scroller>
	  <div class="section-divider">
		<h2 style="color: #F2BC51">Explore the books</h2>
		{#if mobile}
			<p style="color: white; font-size: 24px">
				Explore my reviews for each book below!
			</p>
		{:else}
		<p style="color: white; font-size: 24px">
			I reviewed every book. Click on a bubble to explore!
		</p>
		{/if}
	  </div>
	<div class='{mobile ? "hide": "container"}'>
		<p>Sort the books</p>
	<button on:click={() => handleClick("all")}>
		all
	</button>
	<button on:click={() => handleClick("month")}>
		month
	</button>
	<button on:click={() => handleClick("rating")}>
		rating
	</button>
	<button on:click={() => handleClick("genre")}>
		genre
	</button>

	
	  <div id={filterVizId} bind:offsetWidth={width}>
		{#if width}
			{#if data}
				<Bubbles on:bubHover={showDetail} current={view} dataset={data} w={width} rootid={filterVizId}></Bubbles>
			{/if}
		{/if}
		</div>
		<DetailCard book={bookData}></DetailCard>
	</div>
	
	<div>
		<button class:active={active} style="float: middle; margin-top: 20px" on:click="{() => active = !active}">
			{#if !active}
			&#10004 show all the books in a list?
			{:else}
			&#10008 hide full list below
			{/if}
		</button>
	</div>

	<div class="share-container">
		<h3>Give it a share</h3>
		<Email subject="{title}" body="{desc} {url}" />
		<Reddit class="share-button" {title} {url} />
		<LinkedIn class="share-button" {url} />
		<WhatsApp class="share-button" text="{title} {url}" />
		<Twitter class="share-button" text="{title}" {url} />
	</div>
	
	<!-- start book list -->
	<div>
		{#if data && active}
		<div class="flex-row">
			{#each data as book}
					<div class="flex-col">
						<DetailCard book={book}></DetailCard>
					</div>	
			{/each}
		</div>
		{/if}
	</div>
	<div class="footer">
		<div class="footer-text">
			<h2>Design notes</h2>
			<p>If ya know, ya know. If you don't, stop reading this and watch the beautifully designed and art-directed Netflix series "To All the Boys I've Loved Before". Then come back. Trust. That was pretty much my design process.</p>
		</div>
		<div class="footer-text">
			<h2>Dev notes</h2>
			<p>This was my first project using <a href="https://svelte.dev/" target="_blank">Svelte</a> from start to finish, and wow! Loved it. There are many places it could be more Svelte-y, but for a starter project the development process was very smooth. Svelte made the scrolly transitions easy with a native scroller plugin. The bubbles are some D3 force shenanigans inspired by <a target="_blank" href="https://bl.ocks.org/SpaceActuary/d6b5ca8e5fb17842d652d0de21e88a05">this block</a>.</p>
		</div>
	</div>
</main>

